
#app {
  font-family: 'Hammersmith One', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #111110;
  background: #161616;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0%;
  top: 0%
}
.wrapper {
  position: relative;
  height: 100%;
  max-width: 100%;
  max-height: 100%
}

.editor {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  position: relative;
}
.editor .top {
    height: 28px;
    line-height: 28px;
    background: rgba(0, 0, 0, 0.2);
    width: 80%;
    text-align: left;
    color: white;
    padding: 0px 15px;
}
.editor .mid {
    white-space: nowrap;
    font-size: 0;
    height: 100%;
    width: 100%;
    text-align: left;
}
.editor .mid .main {
      position: relative;
      width: 80%;
      height: 100%;
      display: inline-block;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      padding: 25px 15px;
}
.editor .mid .main canvas {
        width: 100%;
        height: 100%;
        background: #1e1e1d;
        -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
                box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
}
.editor .mid .menu {
      position: absolute;
      top: 0;
      right: 0;
      display: inline-block;
      vertical-align: top;
      overflow-y: auto;
      overflow-x: hidden;
      width: 20%;
      height: 100%;
      max-height: 100%;
      font-size: 14px;
      text-align: left;
      padding: 0 10px;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      background: #161615;
      -webkit-box-shadow: 0 8px 17px 2px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
              box-shadow: 0 8px 17px 2px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
}
.editor .mid .menu .group {
        margin-top: 10px;
        padding: 10px 10px 5px 10px;
        border: 2px solid rgba(244, 244, 230, 0.05);
        border-style: dashed;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        color: #f4f4e6;
}
.editor .mid .menu .group .tip {
          color: rgba(255, 255, 255, 0.5);
          text-align: center;
          width: 100%;
          display: block;
}
.editor .mid .menu .group .group_name {
          display: block;
          font-size: 18px;
          padding: 5px 0 15px 0;
          text-align: center;
}
.editor .mid .menu .group .button {
          position: relative;
          background: #d66d40;
          text-decoration: none;
          padding: 5px 0;
          -webkit-box-sizing: border-box;
                  box-sizing: border-box;
          width: 100%;
          border-radius: 5px;
          border: 1px solid rgba(0, 0, 0, 0.1);
          cursor: pointer;
}
.editor .mid .menu .group .button span {
            text-align: center;
            display: block;
            padding: 10px;
            font-size: 14px;
            font-weight: 700;
            -webkit-user-select: none;
               -moz-user-select: none;
                -ms-user-select: none;
                    user-select: none;
}
.editor .mid .menu .group .button input {
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            opacity: 0;
}
.editor .mid .menu .group .property {
          display: table;
          white-space: nowrap;
          padding: 6px;
          width: 100%;
          -webkit-box-sizing: border-box;
                  box-sizing: border-box;
          border: 2px solid rgba(244, 244, 230, 0.02);
          margin: 2px 0 4px 0;
          white-space: nowrap;
          font-size: 0;
}
.editor .mid .menu .group .property.file_text {
            overflow: scroll;
            max-height: 500px;
            height: 500px;
            width: 100%;
            max-width: 100%;
            font-size: 12px;
            color: rgba(255, 255, 255, 0.5);
            font-family: 'Consolas';
}
.editor .mid .menu .group .property.objects {
            background: rgba(255, 255, 255, 0.05);
            border-radius: 5px;
            cursor: pointer;
}
.editor .mid .menu .group .property.objects span {
              cursor: text;
}
.editor .mid .menu .group .property.objects button.delete {
              background: rgba(0, 0, 0, 0.2);
              color: rgba(244, 244, 230, 0.5);
              border: none;
              border-radius: 45%;
              text-align: center;
              white-space: nowrap;
              height: 22px;
              width: 22px;
              margin-left: 5px;
              padding: 0 0 0 0.7px;
              float: right;
}
.editor .mid .menu .group .property .input {
            display: table;
            z-index: 999;
            min-width: 12px;
            width: 128px;
            text-align: right;
            background: rgba(244, 244, 230, 0.1);
            border: 1px solid rgba(244, 244, 230, 0.2);
            border-radius: 5px;
            -webkit-box-sizing: border-box;
                    box-sizing: border-box;
            position: relative;
}
.editor .mid .menu .group .property .input.tips input {
              width: 50%;
}
.editor .mid .menu .group .property .input input {
              display: table-cell;
              width: 100%;
              background: transparent;
              border: none;
              color: white;
              padding: 5px;
              -webkit-box-sizing: border-box;
                      box-sizing: border-box;
}
.editor .mid .menu .group .property .input span {
              display: block;
              color: rgba(244, 244, 230, 0.5);
              background: rgba(0, 0, 0, 0.2);
              padding: 1px 4px 1px 4px;
              border-radius: 5px;
              -webkit-user-select: none;
                 -moz-user-select: none;
                  -ms-user-select: none;
                      user-select: none;
}
.editor .mid .menu .group .property__button .name, .editor .mid .menu .group .property__button .value {
            vertical-align: middle !important;
}
.editor .mid .menu .group .property .name, .editor .mid .menu .group .property .value {
            display: table-cell;
            vertical-align: top;
            font-size: 14px;
}
.editor .mid .menu .group .property .name {
            font-weight: 500;
}
.editor .mid .menu .group .property .value {
            text-align: right;
}
.editor .mid .menu .group .property .value .button {
              width: 128px;
              padding: 0px;
              display: inline-block;
}
.editor .mid .menu .group .property .value .button span {
                font-size: 12px;
}
.editor .mid .menu .group .property .value > .input {
              display: block;
              float: right;
}
.editor .mid .menu .group .property .value .inputs {
              text-align: right;
}
.editor .mid .menu .group .property .value .inputs.x3 .row {
                margin: 2px 0 4px 0;
}
.editor .mid .menu .group .property .value .inputs.x3 .row label {
                  background: rgba(244, 244, 230, 0.1);
                  height: 18px;
                  width: 18px;
                  border-radius: 50%;
                  text-align: center;
                  line-height: 20px;
                  margin: 0;
                  padding: 0;
                  color: rgba(244, 244, 230, 0.5);
                  margin-right: 5px;
                  -webkit-user-select: none;
                     -moz-user-select: none;
                      -ms-user-select: none;
                          user-select: none;
}
.editor .mid .menu .group .property .value .inputs.x3 .row .input, .editor .mid .menu .group .property .value .inputs.x3 .row label, .editor .mid .menu .group .property .value .inputs.x3 .row span {
                  display: inline-block;
                  vertical-align: middle;
                  font-size: 12px;
}

li {
  position: relative;
  padding: 0 8px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: rgba(255, 255, 255, 0.8);
  cursor: pointer;
  white-space: nowrap;
  text-align: left;
  display: table;
}
li input {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
li label {
    cursor: pointer;
    display: table-cell;
}
li p {
    display: table-cell;
    margin: 0;
    padding: 0;
}
li span {
    color: rgba(255, 255, 255, 0.5) !important;
}
li .right {
    display: table-cell;
    width: 100%;
    text-align: right;
    padding-left: 8px;
}
li .arrow {
    float: right;
}
li:hover {
    color: white;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
ul > li {
    display: inline-block;
}
ul > li:hover {
      background: rgba(0, 0, 0, 0.5);
}
ul > li:hover > ul {
        display: block;
}
ul > li > ul {
      position: absolute;
      line-height: 22px;
      font-weight: 100;
      top: 100%;
      left: 0;
      z-index: 1000000;
      background: rgba(0, 0, 0, 0.5);
      display: none;
      padding: 0 0 8px 0;
}
ul > li > ul label {
        display: block;
        padding: 8px 8px 0 8px;
        color: rgba(255, 255, 255, 0.5);
        font-weight: 100;
}
ul > li > ul > li {
        cursor: pointer;
        display: block;
        position: relative;
}
ul > li > ul > li:hover {
          background: none;
}
ul > li > ul > li > ul {
          position: absolute;
          left: 100%;
          top: -8px;
          padding: 8px;
}
